*{
    margin: 0;
    color: #3a3b3f;
font-family: roboto,"Noto Sans Myanmar UI",arial,sans-serif;
}
.gmail-login {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 32px;
    background-color: #fafafa;
  }
  
  .gmail-login-logo {
    width: 70px;
    margin-bottom: 5%;

  }
  
  .gmail-login-input {
    width: 80%;
    margin-bottom: 30px;
    margin-top: 30px;
    font-size: 16px;
    border-radius: 4px;
    border: 1px solid #858080;
    padding: 5%;   
    color: #202124;
  }
  
  .gmail-login-button {
    width: 30%;
    padding: 8px;
    font-size: 16px;
    border-radius: 4px;
    background-color: #1a73e8;
    color: #fff;
    border: none;
    cursor: pointer;
  }
  
  .gmail-login-button:hover {
    background-color: #1a73e8;
  }

  .emaillog{
    border: 1px solid #202124;
border-radius: 22px;
padding: 2%;
margin-bottom: 3%;
  }
  .bienvenue{
    font-size: 25px;
    margin-bottom: 3%;
  }
  .suivant{
    display: flex;
    justify-content: space-between;
    width: 100%;
    margin-bottom: 20%;
  }
  .mdp{
    color: #1a73e8;
    line-height: 30px;
    font-size: 15px;
    font-weight:500;
  }
  .gmail-login-checkbox{
    display: flex;
width: 80%;
padding-right: 38px;
margin-bottom: 10%;
font-size: 15px;
font-weight: 500;
  }
  .gmail-login-checkbox input{
    width: 20px;
    margin-right: 9%;

  }
